export default {
	data() {
		return {
			InputBottom: 0,
			chatList:[],
			uid: null,
			content: '',
			loadText:'正在获取消息'
		};
	},
	onLoad(e) {
		this.uid = e.receiveId;
		this.getChatList();
	},
	methods: {
		getChatList(){
			uni.request({
				url: this.prefix + "/getMerChats",
				data:{
					uid : this.uid,
					mid : this.getMerchantInfo().id
				},
				success: (res) => {
					this.chatList = res.data.data;
				}
			})
		},
		send(){
			if(!this.content){
				uni.showToast({
					title:'请输入有效的内容',
					icon:'none'
				})
				return;
			}
			
			uni.request({
				url: this.prefix + "/doSendReply",
				data:{
					receiveId: this.uid,
					mid : this.getMerchantInfo().id,
					content : this.content
				},
				success: (res) => {
					if(res.data.code == 200){
						this.content = '';
						this.getChatList();
						uni.pageScrollTo({
						    scrollTop: 999999,	// 设置一个超大值，以保证滚动条滚动到底部
						    duration: 0
						});
					}else{
						uni.showToast({
							title:"发送失败",
							icon:"none"
						})
					}
				}
			})
		}
	}
}
